'use client';

import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { toast } from 'react-hot-toast';

const API_BASE_URL = 'http://127.0.0.1:8000/api';

interface Document {
  id: string;
  filename: string;
  file_type: string;
  created_at: string;
}

export default function DocumentList({ onSelectDoc }: { onSelectDoc: (id: string) => void }) {
  const [documents, setDocuments] = useState<Document[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchDocuments = async () => {
      try {
        setLoading(true);
        const res = await axios.get(`${API_BASE_URL}/documents`);
        setDocuments(res.data.documents || []);
      } catch (error: any) {
        console.error('Fetch Error:', error);
        toast.error('Failed to fetch documents.');
      } finally {
        setLoading(false);
      }
    };

    fetchDocuments();
  }, []);

  if (loading) return <p>Loading documents...</p>;

  return (
    <div>
      <h2 className="text-xl font-semibold mb-2 text-gray-700">Uploaded Documents</h2>
      {documents.length === 0 ? (
        <p className="text-gray-500 text-sm">No documents uploaded yet.</p>
      ) : (
        <ul className="space-y-2">
          {documents.map((doc) => (
            <li
              key={doc.id}
              onClick={() => onSelectDoc(doc.id)}
              className="p-3 border rounded-md hover:bg-gray-100 cursor-pointer transition"
            >
              <p className="font-medium text-gray-800">{doc.filename}</p>
              <p className="text-sm text-gray-500">
                Type: {doc.file_type} | {new Date(doc.created_at).toLocaleString()}
              </p>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
