'use client';

import React, { useState } from 'react';
import axios from 'axios';
import { toast } from 'react-hot-toast';

const API_BASE_URL = 'http://127.0.0.1:8000/api';

export default function AskQuestionBox({ docId }: { docId: string }) {
  const [question, setQuestion] = useState('');
  const [answer, setAnswer] = useState('');
  const [loading, setLoading] = useState(false);

  const handleAsk = async () => {
    if (!question.trim()) return toast.error('Please enter a question.');

    try {
      setLoading(true);
      const res = await axios.post(`${API_BASE_URL}/questions/ask`, {
        doc_id: docId,
        question,
      });
      setAnswer(res.data.answer);
      toast.success('âœ… Answer received!');
    } catch (error: any) {
      console.error('Ask Error:', error);
      toast.error(error?.response?.data?.detail || 'Failed to get answer.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h2 className="text-xl font-semibold mb-3 text-gray-700">Ask a Question</h2>
      <textarea
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        placeholder="Type your question here..."
        className="w-full border border-gray-300 rounded-md p-2 mb-3"
      />
      <button
        onClick={handleAsk}
        disabled={loading}
        className={`w-full bg-green-600 text-white py-2 rounded-md hover:bg-green-700 transition ${
          loading ? 'opacity-60 cursor-not-allowed' : ''
        }`}
      >
        {loading ? 'Thinking...' : 'Ask'}
      </button>

      {answer && (
        <div className="mt-4 bg-gray-50 p-3 rounded-md border">
          <p className="font-medium text-gray-800">Answer:</p>
          <p className="text-gray-700 mt-1">{answer}</p>
        </div>
      )}
    </div>
  );
}
